import { Component, Input, OnInit } from '@angular/core';

import { AngularFirestore } from '@angular/fire/firestore';

import { UpdaterecordComponent } from '../components/updaterecord/updaterecord.component';
import { AlertController, ModalController, NavController } from '@ionic/angular';

import { Camera, CameraOptions } from '@ionic-native/camera';
import { Firebase } from '@ionic-native/firebase';


@Component({
  selector: 'app-tab1',
  templateUrl: 'tab1.page.html',
  styleUrls: ['tab1.page.scss']
})
export class Tab1Page implements OnInit {

  doc:any;
  records: { id: string; type: string; description: string; amount: number;  }[];
  addrecord: {type: string; description: string; amount: number};
  captureDataUrl : string;

  @Input("UserURI") UserURI: Boolean = true;

  constructor(
    private firestore: AngularFirestore,
    private modalCOntroller: ModalController,
  ) {}

  ngOnInit(){
    this.addrecord = {type : '', description :'', amount : null}
    this.firestore.collection('/Records/').snapshotChanges().subscribe(res=>{
      if (res) {
        this.records = res.map(e=>{
          return{
            id: e.payload.doc.id,
            description: e.payload.doc.data()['description'],
            amount: e.payload.doc.data()['amount'],
            type: e.payload.doc.data()['type']
          }
        })
      }
    })
  }

  getPicture(sourceType){
    const cameraOptions: CameraOptions = {
      quality: 50,
      destinationType: Camera.DestinationType.DATA_URL,
      encodingType: Camera.EncodingType.JPEG,
      mediaType: Camera.MediaType.PICTURE,
      sourceType: sourceType
    };

    Camera.getPicture(cameraOptions)
      .then((captureDataUrl) => {
      this.captureDataUrl = 'data:image/jpeg;base64' + captureDataUrl;
    }, (err) => {
      console.error();
    });
  }

  AddRecord(type, description, amount){
    let addrecord = {}
    this.addrecord['type'] = type
    this.addrecord['description'] = description
    this.addrecord['amount'] = amount
    console.log(this.addrecord)
    this.firestore.collection('/Records/').add(this.addrecord).then(()=>{
      this.addrecord = {type : '', description : '', amount : null}
    })
  }

  async UpdateRecord(id, type, description, amount){
    const modal = await this.modalCOntroller.create({
      component: UpdaterecordComponent,
      cssClass: 'my-custom-class',
      componentProps: {
        'id' : id,
        'type' : type,
        'description' : description,
        'amount' : amount,
      }
    });
    return await modal.present();
  }
  DeleteRecord(id){
    this.firestore.doc('/Records/'+id).delete();
  }

}
